

































import StarRating from "vue-star-rating";
import { Vue } from "vue-property-decorator";

export default Vue.extend({
  name: "PresentationRateStars",
  components: { StarRating },
  props: ["value"],
  data: function() {
    return {
      rate: this.value
    };
  },
  methods: {
    setStars() {
      this.$emit("input", this.rate);
    }
  }
});
