















































import { Component, Vue, Watch } from "vue-property-decorator";
import Box from "@/components/Box.vue";
import PageHeader from "@/components/PageHeader.vue";
import Contact from "@/components/Contact.vue";
import axios from "axios";
import { Participant } from "@/types";
import { Html5QrcodeScanner } from "html5-qrcode";

@Component({
  components: { PageHeader, Box, Contact }
})
export default class ScannerChrome extends Vue {
  public inputValue: string = "";
  public participant: Participant | null = null;
  public error: string | null = null;

  get id() {
    const { id } = this.$route.params;
    return id;
  }

  public openScanner() {
    let html5QrcodeScanner = new Html5QrcodeScanner(
        "reader",
        { fps: 10, qrbox: { width: 250, height: 250 } },
        /* verbose= */ false);
    html5QrcodeScanner.render(it => {
      window.location.href = "/new-scanner/" + it;
    }, undefined);

  }

  public mounted() {
    this.scan(this.id);
    new Html5QrcodeScanner(
        "reader",
        { fps: 10, qrbox: { width: 250, height: 250 } },
        false);
  }

  @Watch("inputValue")
  public inputChanged(event: string) {
    this.$router.push({
      name: "scanner",
      params: { id: event }
    });
  }

  @Watch("id")
  public idChanged(value: string) {
    this.scan(value);
  }

  public scan(id: string) {
    if (id) {
      this.participant = null;
      this.error = null;
      this.inputValue = "";
      axios
          .post<Participant>(`/api/participants/${id}/arrived`, {})
          .then(value => {
            this.participant = value.data;
          })
          .catch(reason => {
            if (reason.response.status === 409) {
              this.error = "Ju zarejestrowany!";
              this.participant = reason.response.data;
            } else {
              this.error = "Niepoprawny token";
            }
          });
    }
  }
}
